import React, { useState } from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import { Link } from 'react-router-dom';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

// import css
import '../assets/css/MainMenu.css'

// import Logo
import Logo from '../assets/images/Aqua-New.png';

const drawerWidth = 260;
const navItems = [
    { name: 'Home', url: '/' },
    { name: 'About Us', url: '/about-us' },
    { name: 'Hydration', url: '/hydration' },
    { name: 'Contact Us', url: '/contact-us' }
];

function MainMenu(props) {
    const { window } = props;
    const [mobileOpen, setMobileOpen] = useState(false);
    const [animscroll, setanimscroll] = useState(false);

    onscroll = function () { scrollFunction() };
    const scrollFunction = () => {
        if (document.body.scrollTop > 100 || document.documentElement.scrollTop > 100) {
            setanimscroll(true);
        } else {
            setanimscroll(false);
        }
    }

    const handleTop = () => {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    }
    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const drawer = (
        <Box className="mobileMenu h-100" sx={{ textAlign: 'center' }}>
            <div className="d-flex justify-content-end text-white pe-4 pt-4" role="button">
                <CloseIcon onClick={handleDrawerToggle} />
            </div>
            <div className="py-3 mb-3">
                <Link to='/'>
                    <img src={Logo} alt="Logo" className='MenuLogo' />
                </Link>
            </div>
            <div className="border-bottom">
                {navItems.map((item, key) => (
                    <Link key={key} className='mobilemenuitem' to={item.url}>{item.name}</Link>
                ))}
            </div>
        </Box>
    );

    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <>
            <Box className="MainMenu">
                <AppBar component="nav" className={`desktopMenu bg-white ${animscroll ? 'scroll_padding' : 'position-relative py-2'}`}>
                    <Toolbar className='container-xl'>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="start"
                            className="togglebutton"
                            onClick={handleDrawerToggle}
                            sx={{ mr: 2, display: { md: 'none' } }}
                        >
                            <MenuIcon className="icon" />
                        </IconButton>
                        <Typography
                            sx={{ flexGrow: 1 }}
                        >
                            <Link to='/'>
                                <img src={Logo} alt="Logo" className='MenuLogo' />
                            </Link>
                        </Typography>
                        <Box sx={{ display: { xs: 'none', md: 'block' } }}>
                            {navItems.map((item, key) => (
                                <Link key={key} className='mobilemenuitem' to={item.url}>{item.name}</Link>
                            ))}
                        </Box>
                    </Toolbar>
                    {
                        !animscroll
                            ?
                            <div className="headerShape"></div>
                            :
                            null
                    }
                </AppBar>
                <Box component="nav">
                    <Drawer
                        container={container}
                        variant="temporary"
                        open={mobileOpen}
                        onClose={handleDrawerToggle}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                        sx={{
                            display: { md: 'none', xs: 'block' },
                            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
                        }}
                    >
                        {drawer}
                    </Drawer>
                </Box>
            </Box>
            {
                animscroll
                    ?
                    <button onClick={handleTop} className='goto-top'>
                        <KeyboardArrowUpIcon /></button>
                    :
                    null
            }

        </>
    );
}

export default MainMenu;
