import React, { useState } from 'react';
// api
import axios from 'axios';

// import material packages
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';


function ContactForm() {
    const [state, setState] = useState({
        firstName: '',
        email: '',
        phone: '',
        subject: '',
        msg: '',
    });
    const API_PATH = 'https://aquadivita.com/api/contact-us.php';
    const handleSubmit = e => {
        e.preventDefault();
        axios({
            method: "post",
            url: `${API_PATH}`,
            headers: { "content-type": "application/json" },
            data: state
        })
            .then(result => {
                if (result.data.sent) {
                    initialState();
                    setsuccessMsg("Message Has Been Sent SuccessFully")
                } else {
                    seterrMsg('Message Not Sent');
                }
            })
            .catch(error => {
                seterrMsg('Message Not Sent');
            });
    };
    const initialState = () => {
        setState({
            ...state,
            firstName: '',
            email: '',
            phone: '',
            subject: '',
            msg: '',
        })
    }
    const [successMsg, setsuccessMsg] = useState();
    const [errMsg, seterrMsg] = useState();
    return (
        <div className="col-md-8 mb-5">
            <div>
                <div className="mb-5">
                    <h3 className='fw-bold'>Contact Form</h3>
                </div>
                <form onSubmit={handleSubmit}>
                    <div className="row">
                        <div className="col-md-6 mb-4">
                            <TextField value={state.firstName} onChange={(text) => setState({ ...state, firstName: text.target.value })} className='w-100' label="Your Name" variant="outlined" required />
                        </div>
                        <div className="col-md-6 mb-4">
                            <TextField type='email' value={state.email} onChange={(text) => setState({ ...state, email: text.target.value })} className='w-100' label="Email Address" variant="outlined" required />
                        </div>
                        <div className="col-md-6 mb-4">
                            <TextField value={state.phone} onChange={(text) => setState({ ...state, phone: text.target.value })} className='w-100' label="Phone Number" variant="outlined" required />
                        </div>
                        <div className="col-md-6 mb-4">
                            <TextField value={state.subject} onChange={(text) => setState({ ...state, subject: text.target.value })} className='w-100' label="Subject" variant="outlined" required />
                        </div>
                        <div className="col-12 mb-4">
                            <TextField className='w-100' value={state.msg} onChange={(text) => setState({ ...state, msg: text.target.value })} multiline rows={5} label="Leave a Comment" defaultValue='' required />
                        </div>
                        {successMsg &&
                            <div className="col-12 mb-4">
                                <Alert severity="success" variant="filled"> {successMsg}</Alert>
                            </div>
                        }
                        {errMsg &&
                            <div className="col-12 mb-4">
                                <Alert severity="error" variant="filled"> {errMsg}</Alert>
                            </div>
                        }
                        <div className="d-flex justify-content-end mb-4">
                            <Button type='submit' variant="contained" className='py-3 submit-btn px-5 rounded-pill'>Send Now</Button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default ContactForm;